import React from 'react';

export default ({ data }) => {
	console.log(data);
	return (
  <div>
    <h1>My Site Files</h1>
    <table>
      <thead>
        <tr>
          <th>relativePath</th>
          <th>prettySize</th>
          <th>extension</th>
          <th>birthTime</th>
        </tr>
      </thead>
      <tbody>
        {data.allFile.edges.map(({ node }) => (
          <tr key={node.relativePath}>
            <td>{node.relativePath}</td>
            <td>{node.prettySize}</td>
            <td>{node.extension}</td>
            <td>{node.birthTime}</td>
          </tr>
					))}
      </tbody>
    </table>
  </div>
	);
};

export const query = graphql`
	query MyFilesQuery {
		site {
			siteMetadata {
				title
			}
		}
		allFile {
			edges {
				node {
					relativePath
					prettySize
					extension
					birthTime(fromNow: true)
				}
			}
		}
	}
`;



